.page-header {
  position: fixed;
  padding: 0 50px;
  padding-top: 30px;
  padding-bottom: 35px;
  width: 100%;
  background: $color-bg-header;
  z-index: 3;

  @include vp-1023 {
    padding: 0;
    padding-top: 20px;
    padding-bottom: 25px;
  }

  @include vp-769 {
    padding-top: 15px;
    padding-bottom: 20px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.page-header__logo img {
  width: 215px;
  height: 55px;

  @include vp-769 {
    width: 35px;
    height: 35px;
  }
}

.page-header__contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 15px;
  row-gap: 15px;
  margin-left: auto;
  width: 453px;

  @include vp-1023 {
    padding-top: 10px;
    width: 90px;
    margin-right: 14px;
  }

  @include vp-769 {
    padding-top: 5px;
    margin-right: 5px;
  }
}

.page-header__contact p {
  margin: 0;
  padding: 0;
  line-height: 18px;
  color: $color-alice-blue;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  @include vp-1023 {
    display: none;
  }
}

.page-header__contact a {
  position: relative;
  width: 219px;
}

.page-header__contact svg {
  position: absolute;
  left: -36px;
}

.page-header__email {
  margin-left: 29px;
}

.page-header__email:hover,
.page-header__phone:hover,
.page-header__email:focus-within,
.page-header__phone:focus-within {
  opacity: 0.7;
  fill-opacity: 0.7;
}

.page-header__button {
  display: block;
  margin-top: 4px;
  margin-left: auto;
  margin-right: 60px;
  padding-top: 11px;
  padding-bottom: 12px;
  width: 160px;
  font-style: 14px;
  line-height: 16px;
  letter-spacing: 0.05px;
  color: $color-alice-blue;
  background-color: transparent;
  border: 1px solid $color-alice-blue;

  @include vp-1023 {
    display: none;
  }
}
